.contactBox {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}
.contactSearch {
	width: 100%;
	position: relative;
}

.contactSearchIcon {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 17px;
	z-index: 1;
}

.navTitle {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.headingContainer {
	margin-left: 5px;
	flex: 1;
}

.backIcon {
	margin-right: 5px;
}

.contactSearchInput {
	padding-left: 44px !important;
}

.contactSearchInput::placeholder {
	color: rgba(20, 20, 20, 0.3);
}

.contactAddContainer {
	border-radius: 4px;
	cursor: pointer;
	height: 48px;
	margin-left: 12px;
	padding: 0 12px 0 15px;
	background: var(--bg-white);
}

.contactAddTxt {
	padding-left: 8px;
	font-size: 14px;
}

/* List */
.contactList {
	border-radius: 4px;
	padding: 0;
}
.contactListHorizontal {
	padding: 0;
	display: flex;
	flex-wrap: nowrap;
	gap: 15px;
	overflow-x: auto;
}

.contactListItem {
	border-bottom: 1px solid var(--border-light);
	background: var(--bg-white);
	cursor: pointer;
	display: flex;
	align-items: center;
	padding: 16px;
}
.contactListItemHorizontal {
	width: 100px;
	min-width: 100px;
	border: 1px solid var(--border-light);
	border-radius: 4px;
	cursor: pointer;
	display: flex;
	align-items: center;
	padding: 16px 10px 10px 10px;
	flex-direction: column;
	background-color: var(--bg-white);
}

.contactListItem:last-of-type {
	border: none;
}

.contactListInitials {
	background-color: var(--initials-bg);
	border-radius: 4px;
	font-weight: 500;
	min-width: 37px;
	min-height: 37px;
	padding: 5px 10px;
	text-transform: uppercase;
}
.contactListInitialsHorizontal {
	font-size: 16px;
	width: 54px;
	height: 54px;
}

.contactListName {
	margin-right: auto;
	text-align: center;
	padding-left: 15px;
}
.contactListNameHorizontal {
	line-height: 18px;
	overflow: hidden;
	text-overflow: ellipsis;
	text-align: center;
	padding: 15px 0 0 0;
	margin: 0;
}
.scrollableDiv {
	overflow-y: auto;
	flex: 1 1 auto;
	/* height: calc(100% - 110px); */
}
.scrollableDivHorizontal {
	overflow-x: auto;
	display: flex;
    flex-wrap: nowrap;
}
div.bodyStyles {
	padding: 0;
	margin: 0;
	overflow-y: hidden;
	/* background: var(--bg-light); */
}

.p15 {
	padding: 15px;
}

.contactNavContainer {
	background: var(--bg-dark);
	border-color: var(--nav-header-border);
}